A;IACI;AACJ;AAEA;IACI;AACJ;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,WAAW;IACX,uBAAuB;IACvB,SAAS;IACT,mBAAmB;IACnB,yBAAyB;IACzB,8BAA8B;IAC9B,6DAA6D;IAC7D,yBAAyB;IACzB,8BAA8B;IAC9B,6BAA6B;IAC7B,4BAA4B;IAC5B,wBAAwB;IACxB,iBAAiB;IACjB,8BAA8B;AAElC;AAGA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,sBAAsB;IACtB,cAAc;IACd,UAAU;AACd;AAEA;IACI,aAAa;IACb,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,YAAY;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from '@vue/reactivity';\nimport { defineAsyncComponent } from 'vue';\nimport { container } from '../../../../domain/core/diContainer';\nimport { IHeaderButton } from '../../../../domain/model/ui/IHeaderButton';\nimport { IDialog } from '../../../../domain/presentation/IDialog';\nimport { INotification } from '../../../../domain/presentation/INotification';\nimport { IStudio } from '../../../../domain/useCase/IStudio';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport 'bootstrap/js/dist/dropdown'\nimport { IApplication, IExportViewItem } from '../../../../domain/model/models';\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport { ITab, ITabs } from '../../../vue3/components/ui/ITabs'\nimport { ref } from 'vue';\nimport { IGetExportSelectionData } from '../../../../domain/useCase/IGetExportSelectionData';\nimport { IRouter } from '../../../../domain/presentation/IRouter';\nimport ExportApp from \"../../components/application/exportApp.vue\";\nimport { ILocalization } from '../../../../domain/infrastructure/ILocalization';\nimport { ExportType } from '@stechquick/algae/lib/qCloudTemp/exporter';\nimport { ILogout } from '../../../../domain/useCase/ILogout';\nimport { TooltipImpl } from '../../impls/tooltipImpl';\nimport { ITimer } from '../../../../domain/infrastructure/ITimer';\n\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst router = container.resolve<IRouter>(IRouter);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst timer = container.resolve<ITimer>(ITimer);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst checkedOut = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst props = defineProps<{ app: IApplication }>();\nconst deployTip = ref(\"\");\nconst deployTooltipVisbile = ref(false);\nconst deployContentOpacity = ref(1)\nconst hoverValue = ref(false);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\nlet deployBtnTimeout: NodeJS.Timeout | undefined;\nconst tablist = computed(() => {\n    return viewModel.studio?.openItems?.map(item => {\n        const retVal: ITab = {\n            name: item.name,\n            type: item.modelType,\n            id: item.ID,\n            selected: viewModel.studio.currentItem?.ID == item.ID,\n            modified: item.modified?.model,\n            synced: item.synced,\n            cb: {\n                close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n                select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n            }\n        };\n        return ref(retVal).value;\n    }) || [];\n});\n\nfunction onCheckout() {\n    const currentItem = viewModel.studio.currentItem;\n    if (!currentItem) { return; }\n\n    executor.execute(async () => studio.checkout(currentItem), { loading: true });\n}\nfunction onSave() {\n    const currentItem = viewModel.studio.currentItem;\n    if (!currentItem) { return; }\n\n    executor.execute(async () => studio.save(currentItem), { loading: true });\n}\nfunction onSavePlus() {\n    dialog.showDialog(defineAsyncComponent(() => import(\"../dialogs/studio/savePlus.vue\")), { closable: true });\n}\nfunction onItemSelect(selectedItem: IHeaderButton) {\n    selectedItem.onSelect();\n}\n\nasync function release() {\n    executor.execute(async () => studio.release(viewModel.studio.currentItem!), { loading: false });\n}\nfunction listExportJobs() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../export/exportJobList.vue`)), { closable: true, title: \"Export Job List\", width: '990px', maxHeight: '500px', overflow: 'auto' }, {jobType: \"export\"});\n}\n\nfunction listDeployJobs() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../export/exportJobList.vue`)), { closable: true, title: \"Deploy Job List\", width: '990px', maxHeight: '500px', overflow: 'auto' }, {jobType: \"deploy\"});\n}\n\nasync function openExportDialog(exportType: ExportType) {\n    executor.execute(async () => {\n        const applicationID = routerParams.appID;\n        selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n        dialog.showDialog(ExportApp, { closable: true, title: 'Export-' + viewModel.studio.appName, image: { src: props.app?.logo || '../../../../images/is-bankasi-logo.jpg', width: '50px', height: '50px' }, width: '1000px', maxHeight: '500px', overflow: 'auto' }, { selectionData: selectionData.value, exportType });\n\n    }, { loading: true });\n\n}\nfunction goMainPage() {\n    router.push(\"/main/applicationList\");\n}\n\nfunction checkDeployConditions() {\n    return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;\n}\n\nfunction logout() {\n    executor.execute(async () => {\n        await executor.executeUseCase<ILogout>(ILogout);\n        router.push(\"/login\");\n    }, { loading: true });\n}\n\nfunction showDeployTip() {\n    if (checkDeployConditions()) {\n        deployTip.value = localization.get(\"deployTip\");\n        return deployTip.value\n    } else {\n        deployTip.value = \"\"\n        return deployTip.value\n    }\n}\n\nfunction openDeployScreen() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../dialogs/deploy/deployScreen.vue`)), { closable: true, title: \"Deploy\", width: \"1417px\", height: \"741px\" })\n}\n\nfunction hoverLogo() {\n    hoverValue.value = true\n}\nfunction unHover() {\n    hoverValue.value = false\n}\nfunction deployBtnTooltipOver() {\n    deployTooltipVisbile.value = true\n    deployContentOpacity.value = 1;\n    timer.reset()\n}\nfunction deployTooltipMouseLeave() {\n    deployTooltipVisbile.value = false\n}\nfunction deployBtnTooltipMouseLeave() {\n    if (timer.isTimerRunning) {\n        return\n    }\n    timer.setUp({\n        time: 3000, callback: () => {\n            deployTooltipVisbile.value = false;\n            deployContentOpacity.value = 1;\n            timer.reset()\n        }, interval: {\n            time: 1000, callback: () => {\n                deployContentOpacity.value = deployContentOpacity.value - 0.3;\n            }\n        }\n    })\n}\nfunction deployContentpOver() {\n    deployContentOpacity.value = 1;\n    timer.reset()\n}\nfunction openSettings() {\n    deployTooltipVisbile.value = false\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)),\n        { closable: true, title: \"Deploy Settings\", width: \"671px\", height: \"497px\" }\n    );\n}\n</script>\n<template>\n\n    <div class=\"top-menu\">\n        <div class=\"row top-container\">\n            <div class=\"col-lg-1 home-section\" style='display: flex;'>\n                <div class=\"corner-container\">\n                    <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\"\n                        :style=\"{ 'background-color': hoverValue ? '#151233' : '', 'padd