,yBAAyB;IACzB,WAAW;IACX,WAAW;AACf;AAEA;IACI,2BAA2B;AAC/B;AAEA;IACI,WAAW;IACX,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;AACtB;AAEA;IACI,WAAW;AACf;AAEA;IACI,sBAAsB;AAC1B;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,iBAAiB;AACrB;AAEA;IACI,aAAa;IACb,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,iBAAiB;IACjB,uBAAuB;AAE3B;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,kBAAkB;IAClB,SAAS;IACT,6BAA6B;IAC7B,uBAAuB;IACvB,iBAAiB;IACjB,WAAW;IACX,UAAU;IACV,mBAAmB;IACnB,8BAA8B;IAC9B,yDAAyD;IACzD,+CAA+C;IAC/C,kCAAkC;IAClC,UAAU;IACV,kBAAkB;IAClB,YAAY;IACZ,YAAY;AAEhB;AAEA;IACI,eAAe;IACf,UAAU;IACV,cAAc;IACd,kBAAkB;AACtB;AAGA;KACK,eAAe;IAChB,UAAU;IACV,cAAc;IACd,kBAAkB;AACtB;AACA;IACI,eAAe;AACnB;AAEA;IACI;AACJ;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,gBAAgB;IAChB,mBAAmB;IACnB,YAAY;AAChB;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,UAAU;IACV,gBAAgB;IAChB,iBAAiB;IACjB,qBAAqB;AACzB","sourcesContent":["<script setup lang=\"ts\">\nimport { defineAsyncComponent, onMounted } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { INavItem } from \"../../../../domain/model/ui/INavItem\";\nimport { ISidebarManager } from \"../../../../domain/objects/ISidebarManager\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { computed, ref } from \"vue\";\nimport { ITreeView, ITreeViewInstances } from \"../../../../domain/presentation/ITreeView\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IFileExplorer } from \"../../../../domain/useCase/IFileExplorer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IModel, IModule, IFolder } from \"../../../../domain/model/models\";\nimport { ModelType } from \"@stechquick/algae/lib/qCloudTemp/quickCloud\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst sidebarSectionElement = ref<HTMLSelectElement>();\nconst ui = viewModel.studio.ui;\n\nconst studio = container.resolve<IStudio>(IStudio);\nconst sidebarManager = container.resolve<ISidebarManager>(ISidebarManager);\nconst item = { name: \"files\", sidebarComponent: \"explorer.vue\", selected: true };\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst navigationItems = viewModel!.studio!.ui?.navItems;\nnavigationItems?.forEach((item) => {\n    onNavItemSelect(item);\n});\ntype ITreeviewFilterData = Record<string, Array<IModel | IFolder>>;\nconst itemsCache = ref<Array<IModel | IModule | IFolder>>([]);\nlet cacheItems = true;\nconst treeviewFilterData = ref<ITreeviewFilterData>({});\nconst pagesFilterData = ref<ITreeviewFilterData>({});\nconst entitiesFilterData = ref<ITreeviewFilterData>({});\nconst processesFilterData = ref<ITreeviewFilterData>({});\nconst resetSideBar = ref(false);\nconst showPages = ref(true);\nconst showEntities = ref(true);\nconst showProcesses = ref(true);\nconst createPopupVisible = ref(false);\nconst filteredModels = ref<Array<ModelType>>([]);\nconst searchInput = ref(\"\");\n\nlet initialMenu = document.getElementsByClassName(\"vertical-nav-studio\")[0];\nlet expandedMenu = document.getElementsByClassName(\"vertical-nav-expanded\")[0];\nlet closeMenuIcon = document.getElementsByClassName(\"chevron-left\")[0];\nlet expandMenuIcon = document.getElementsByClassName(\"menu-icon\")[0];\nfunction onNavItemSelect(item: INavItem) {\n    if (item.selected && item.sidebarComponent) {\n        studio.nav_unselect();\n        sidebarManager.hide();\n        return;\n    }\n    if (item.sidebarComponent) {\n        sidebarManager.show(item.sidebarComponent);\n    } else {\n        sidebarManager.hide();\n    }\n    studio.nav_selectItem(item);\n}\nfunction openMenu() {\n    (<any>document.getElementsByClassName(\"vertical-nav-studio\")[0]).style = \"display: none\";\n    (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: block\";\n}\nfunction closeMenu() {\n    (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: none\";\n    (<any>document.getElementsByClassName(\"vertical-nav-studio\")[0]).style = \"display: block\";\n}\nonMounted(() => {\n    (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: none\";\n    document.getElementsByClassName(\"menu-icon\")[0].addEventListener(\"click\", openMenu);\n    document.getElementsByClassName(\"chevron-left\")[0].addEventListener(\"click\", closeMenu);\n    openMenu();\n});\n\nfunction moveHandler(event: MouseEvent) {\n    if (!ui) {\n        return;\n    }\n    ui.sidebar.left = event.x - sidebarSectionElement.value!.offsetLeft - 2;\n}\nfunction mousedown(event: MouseEvent) {\n    window.addEventListener(\"mousemove\", moveHandler);\n    event.preventDefault();\n}\nfunction mouseup() {\n    window.removeEventListener(\"mousemove\", moveHandler);\n}\nasync function collapse() {\n    const treeview = await executor.resolveUseCase<ITreeView>(ITreeViewInstances.FileExplorer);\n    treeview.collapseAll();\n}\nasync function addModule() {\n    createPopupVisible.value = false\n    const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n    fileExplorer.onNewItemSelect(viewModel.studio.appId, viewModel.studio.appName, \"application\", \"Module\");\n}\nasync function addScreen() {\n    createPopupVisible.value = false\n    const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n    fileExplorer.onNewItemSelect(viewModel.studio.appId, viewModel.studio.appName, \"application\", \"Screen\");\n}\n\nasync function attachModuleToApp() {\n    createPopupVisible.value = false\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/attachModuleToApp.vue`)),\n        {\n            closable: true,\n            title: \"Add Existing Module\",\n            width: \"1000px\",\n            closeOnOutClick: true,\n        }\n    );\n}\n\nconst leftpx = computed(() => {\n    if (!ui) {\n        return undefined;\n    }\n    return ui.sidebar.left + \"px}\";\n});\n\nfunction openSettings() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)),\n        { closable: true, title: \"Deploy Settings\", width: \"671px\", height: \"497px\" }\n    );\n}\n\nfunction filterTreeview() {\n    if(itemsCache.value.length < viewModel.studio.items.length) {\n        itemsCache.value = itemsCache.value.concat(viewModel.studio.items.filter(item => itemsCache.value.findIndex(i => i.ID == item.ID) == -1))\n    } else if(itemsCache.value.filter.length > viewModel.studio.items.length) {\n        itemsCache.value = itemsCache.value.filter(item => item.objectType != \"module\" || viewModel.studio.items.findIndex(i => i.ID == item.ID) != -1)\n    }\n    treeviewFilterData.value = {};\n    pagesFilterData.value = {};\n    entitiesFilterData.value = {};\n    processesFilterData.value = {};\n    \n    itemsCache.value.forEach((item) => {\n        switch (item.objectType) {\n            case \"module\":\n                pagesFilterData.value[item.ID] = [];\n                entitiesFilterData.value[item.ID] = [];\n                processesFilterData.value[item.ID] = [];\n                (item as IModule).children.forEach((children) => {\n                    if (children.objectType === \"model\") {\n                        if (children.modelType === \"qjson\" || (children as IModel).modelType == \"js\" || (children as IModel).modelType === \"yaml\") {\n                            pagesFilterData.value[item.ID].push(children);\n                        } else if (children.modelType === \"entityDesigner\") {\n                            entitiesFilterData.value[item.ID].push(children);\n                        } else if (children.modelType === \"process\" || children.modelType === \"bpmn\") {\n                            processesFilterData.value[item.ID].push(children);\n                        }\n                    }\n                });\n                break;\n            case \"model\":\n            case \"folder\": // TODO: This case have to be approved when folder system arrived.\n                treeviewFilterData.value[item.ID] = [item];\n                break;\n            default:\n                break;\n        }\n    });\n}\n\nfunction createPopupOpen() {\n    if (createPopupVisible.value == false) {\n        createPopupVisible.value = true\n    }\n    else {\n        createPopupVisible.value = false\n    }\n\n}\n\nfunction toggleFilter(type: \"ui\" | \"entity\" | \"process\") {\n    switch (type) {\n        case \"ui\":\n            viewModel.studio.filters.ui = showPages.value = !showPages.value;\n            break;\n        case \"entity\":\n            viewModel.studio.filters.entity = showEntities.value = !showEntities.value;\n            break;\n        case \"process\":\n            viewModel.studio.filters.process = showProcesses.value = !showProcesses.value;\n            break;\n        default:\n            break;\n    }\n    if (cacheItems) { itemsCache.value = viewModel.studio.items; cacheItems = false; }\n    filterTreeview();\n    showModels();\n}\n\nfunction showModels() {\n    filteredModels.value = [];\n    if (showPages.value) filteredModels.value.push(\"qjson\");\n    if (showEntities.value) filteredModels.value.push(\"entityDesigner\");\n    if (showProcesses.value) {\n        filteredModels.value.push(\"process\");\n        filteredModels.value.push(\"bpmn\");\n    }\n    sidebarManager.reset();\n    viewModel.studio.items = itemsCache.value\n        .map((item) => ({\n            ...item,\n            children: (showPages.value && item.objectType == \"model\" ? treeviewFilterData.value[item.ID] || [] : [])\n                .concat(showPages.value ? pagesFilterData.value[item.ID] || [] : [])\n                .concat(showEntities.value ? entitiesFilterData.value[item.ID] || [] : [])\n                .concat(showProcesses.value ? processesFilterData.value[item.ID] || [] : []),\n        }))\n        .filter((item) => item.objectType == \"module\" || showPages.value);\n\n    sidebarManager.show(item.sidebarComponent);\n}\n\nfunction searchItem() {\n    if (cacheItems) filterTreeview();\n    viewModel.studio.filters.text = searchInput.value;\n    if (searchInput.value.length > 2) {\n        resetSideBar.value = true;\n        viewModel.studio.items = viewModel.studio.items\n            .filter(\n                (item) =>\n                    (item.objectType === \"model\" && item.name.toLocaleLowerCase().includes(searchInput.value.toLocaleLowerCase())) ||\n                    (item.objectType === \"module\" &&\n                        item.children.some((child) => child.name.toLocaleLowerCase().includes(searchInput.value.toLocaleLowerCase())))\n            )\n            .map((item) =>\n                item.objectType === \"module\"\n                    ? {\n                        ...item,\n                        children: item.children.filter((child) => child.name.toLocaleLowerCase().includes(searchInput.value.toLocaleLowerCase())),\n                    }\n                    : item\n            );\n\n        sidebarManager.reset();\n        sidebarManager.show(item.sidebarComponent);\n    } else if (resetSideBar.value) {\n        showModels();\n        resetSideBar.value = false;\n    }\n}\n</script>\n<template>\n    <!-- <div>\n      <img class=\"logo\" src=\"https://www.onplateau.com/img/logo.svg\" />\n    </div> -->\n    <div class=\"contaier\">\n        <div class=\"row\" style=\"height: 100%\">\n            <div class=\"col-3\">\n                <div class=\"vertical-nav-studio\" v-for=\"item in  viewModel!.studio!.ui?.navItems\" :key=\"item.name\">\n                    <div @click=\"onNavItemSelect(item)\">\n                        <i class=\"mdi mdi-menu mdi-24px menu-icon menu-icon-container\"></i>\n                    </div>\n              