   <div class=\"col-12 pencilIcon\">\n                                            <h6 class=\"align-version tree-text ps-3 versionText\">\n                                                version:\n                                                <span v-if=\"node.version == sysLatestWord\"\n                                                    class=\"versionBadge latestVersionBadge\">\n                                                </span>{{ node.version }}\n                                                <i class=\"mdi mdi-pencil-outline ms-1\" @click=\"versionDialog(node)\"></i>\n                                            </h6>\n                                        </div>\n                                    </div>\n                                </template>\n                            </tree>\n\n\n\n                        </div>\n\n                    </div>\n\n                    <div style='display:flex; margin-left: 32px;'>\n                        <h6 style=\"margin-right: 3px;\n                    font-weight: 700;\">* </h6>\n                        <h6 class='latesDesc'>The last major version registered in the system. In case there is no\n                            version, the last version registered in the system</h6>\n                    </div>\n                    <div class=\"row export-btn-container\">\n                        <div class=\"col-12 position-relative\">\n                            <div class=\"position-absolute end-0 me-5 bottom-container\">\n                                <span id=\"selectedModel\" class=\"left-text ps-3 pe-3 pb-2 pt-2 mt-1\">\n                                    <span style=\"    padding-left: 37px;\n    padding-right: 5px;\">{{ checkedItems.length }}</span>\n                                    <span class=\"right-text\">Item\n                                        selected</span>\n                                </span>\n                                <button type=\"button\" class=\"btn-lg export-btn p-1 ms-4 mb-1\"\n                                    @click=\"create()\">EXPORT</button>\n                            </div>\n                        </div>\n                    </div>\n\n                </div>\n            </div>\n\n        </div>\n    </div>\n\n\n</template>\n\n<script setup lang=\"ts\" >\nimport tree from 'vue3-treeview'\nimport \"vue3-treeview/dist/style.css\";\nimport { computed, defineAsyncComponent, ref } from 'vue'\nimport { IExportViewItem, IItemType, IModel } from '../../../../domain/model/models';\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { container } from '../../../../domain/core/diContainer';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { ICreateExportJob } from '../../../../domain/useCase/ICreateExportJob';\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { ExportType, IExportItem } from '@stechquick/algae/lib/qCloudTemp/exporter';\nimport { CryptoHelper } from '@stechquick/algae/lib/helpers/cryptoHelper';\nimport { IJobUtils } from '../../../../domain/useCase/IJobUtils';\nimport { IQJsonAdditionals } from '@stechquick/algae/lib/qCloudTemp/quickCloud';\n\ninterface IData {\n    selectionData: Array<IExportViewItem>,\n    exportType: ExportType\n}\n\ninterface ITreeViewNode {\n    text: string,\n    name: string,\n    children?: Array<string>\n    state: { checked: boolean },\n    id: string;\n    version?: string;\n    versions?: Array<string>;\n    type: IItemType;\n    isSelected: boolean;\n    path: string;\n    size?: number\n    orig: () => IExportViewItem;\n}\n\ntype ITreeViewData = Record<string, ITreeViewNode>;\n\nconst props = defineProps<IData>();\nconst emit = defineEmits([\"close\"]);\nconst items = ref<Array<IExportViewItem>>(props.selectionData.filter(i => i.usageType != \"appSettings\"));\nconst settingsItems = props.selectionData.filter(i => i.usageType == \"appSettings\");\nconst settingsItemsNodeId = CryptoHelper.CreateGuid();\nitems.value.unshift({\n    id: settingsItemsNodeId,\n    name: \"UI Settings\",\n    priority: 0,\n    type: \"module\",\n    children: settingsItems,\n    isSelected: true,\n});\nconst dialog = container.resolve<IDialog>(IDialog);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst treeViewNodes = ref<ITreeViewData>({});\nconst selectedTypeName = ref(props.exportType as unknown as ExportType);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst jobUtils = container.resolve<IJobUtils>(IJobUtils);\nconst sysLatestWord = ref(\"system latest *\");\n\n\nfunction iterateItemsAndFindChildren(items: Array<IExportViewItem>, path: string) {\n    items.forEach((item, index) => {\n        const curNode: ITreeViewNode = treeViewNodes.value[item.id] = {\n            text: item.name,\n            name: item.name,\n            state: { checked: true },\n            id: item.id,\n            version: (item.type != \"folder\" && item.type != \"module\") ? sysLatestWord.value : undefined,\n            type: item.type,\n            isSelected: true,\n            path: item.usageType == \"appSettings\" ? \"\" : path,\n            size: item.size,\n            orig: () => item\n        };\n        if (!item.children) { return; }\n\n        curNode.children = item.children.map(child => child.id);\n        iterateItemsAndFindChildren(item.children, path + \"/\" + item.name);\n    });\n}\niterateItemsAndFindChildren(items.value, \"\");\n\nconst roots = items.value.map(item => item.id);\nconst modules = computed(() => items.value.map(item => item.type == \"module\"));\nconst checkedItems = computed(() => Object.keys(treeViewNodes.value).map(key => treeViewNodes.value[key]).filter(item => item.type != \"module\" && item.type != \"folder\").filter(item => item.state.checked));\nconst checkedModules = computed(() => Object.keys(treeViewNodes.value).map(key => treeViewNodes.value[key]).filter(item => item.type == \"module\").filter(item => item.state.checked));\n\nconst config = ref({\n    roots,\n    checkboxes: true,\n    dragAndDrop: true,\n    editable: true,\n    checkMode: 0\n});\n\n// const checkedKeys = Object.keys(treeViewNodes.value).filter(key => treeViewNodes.value[key].state.checked);\n\n\nfunction versionDialog(clickedNode: ITreeViewNode) {\n    dialog.showDialog(defineAsyncComponent(() => import(\"./modelVersionDialog.vue\")), { closable: true, title: 'Change Version', width: '900px', maxHeight: '500px', overflow: 'auto' }, { clickedNode });\n}\n\nfunction isRootPage(item: IExportViewItem){\n    return item.type == \"qjson\" && (item.modelAdditionals as IQJsonAdditionals).isRootPage;\n}\n\nfunction create() {\n    executor.execute(async () => {\n        await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {\n            app: { ID: viewModel.studio.appId, name: viewModel.studio.appName },\n            exportType: selectedTypeName.value,\n            selectionData: checkedItems.value.map(item => {\n                const origItem = item.orig();\n                return <IExportItem>{\n                    id: item.id,\n                    name: jobUtils.getFileFullname(origItem),\n                    path: item.path,\n                    type: jobUtils.getItemType(origItem),\n                    ver:  item.version == sysLatestWord.value ? \"latest\": item.version,\n                    size: item.size,\n                    isRootPage: isRootPage(origItem),\n                }\n            })\n        });\n        emit(\"close\");\n    }, { loading: true });\n}\n\n</script>\n\n<style scoped>\n.bottom-container {\n    display: flex;\n    align-items: center;\n}\n\n.pencilIcon {\n    text-align: end;\n}\n\n.right-text {\n    color: #757575;\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 20px;\n    letter-spacing: 0px;\n    font-family: 'ROBOTO';\n    border-radius: 4px;\n}\n\n.left-text {\n    background-color: #ECEDF6;\n    border-radius: 4px;\n    height: 48px;\n    width: 240px;\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 500;\n    font-family: 'ROBOTO';\n}\n\n.mdi-pencil-outline {\n    cursor: pointer;\n}\n\n.mdi-pencil-outline {\n    cursor: pointer;\n}\n\n.top-text {\n    display: flex;\n    margin-top: 38px;\n    margin-left: 37px;\n    align-items: center;\n}\n\n.treeviewCard {\n    margin-left: 37px;\n    margin-right: 37px;\n    margin-bottom: 37px;\n    text-align: center;\n    width: 100%;\n    height: 500px;\n    border: 1px solid #D5D5D5;\n    overflow: auto;\n}\n\n.treeviewCard::-webkit-scrollbar {\n    width: 12px;\n}\n\n.treeviewCard::-webkit-scrollbar-track {\n    background: transparent;\n}\n\n.treeviewCard::-webkit-scrollbar-thumb {\n    background-color: #D0D1E9;\n    border-radius: 16px;\n}\n\n.export-btn {\n    background-color: #151233;\n    color: #fff;\n    width: 123px;\n    height: 48px;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 20px;\n    letter-spacing: 0px;\n    font-family: 'ROBOTO';\n    border-radius: 4px;\n    border: unset;\n\n}\n\n.latestVersionBadge {\n    font-size: 17px;\n    padding-top: 8px;\n    margin-right: 3px;\n    font-weight: 700;\n\n}\n\n.versionText {\n    font-size: 15px;\n}\n\n.latesDesc {\n    color: rgb(97, 97, 97);\n}\n\n.export-btn-container {\n    margin-top: 24px;\n}\n</style>"],"sourceRoot":""}]);
